import * as React from "react";
import { useForm } from "react-hook-form";
import emailjs from 'emailjs-com';

type FormData = {
  firstName: string;
  lastName: string;
  phoneNumber: string;
  email: string;
  message: string;
};

export default function App() {
  const {
    register,
    handleSubmit,
  } = useForm<FormData>();

  const [messageSent, setMessageSent] = React.useState(false);

  const onSubmit = handleSubmit((data, e) => {
    if (e) {
      e.preventDefault();
      const form = e.target as HTMLFormElement;
      emailjs.sendForm('service_fezjhag', 'template_7qu5hyl', form, 'D_kbVnjAHNAS_VcWm')
        .then((result) => {
          console.log(result.text);
          setMessageSent(true);
        }, (error) => {
          console.log(error.text);
        });
    }
  });

  return (
    <div>
      {messageSent ? (
        <div>
          <h2 className="text-brown-400 text-3xl p-10">Message Sent!</h2>
        </div>
      ) : (
        <form onSubmit={onSubmit} className="flex flex-col text-brown-400 p-12">
          <label className="p-1">First Name</label>
          <input {...register("firstName")} className="text-black p-1" />

          <label className="p-1">Last Name</label>
          <input {...register("lastName")} className="text-black p-1" />

          <label className="p-1">Phone Number</label>
          <input {...register("phoneNumber")} className="text-black p-1" />

          <label className="p-1">Email</label>
          <input {...register("email")}  className="text-black p-1" />

          <label className="p-1">Message (200 characters max)</label>
          <textarea
            className="text-black p-1"
            {...register("message", { maxLength: 200 })}
            rows={4}
            cols={30}
          />

          <button type="submit" className="p-1">Submit</button>
        </form>
      )}
    </div>
  );
}